@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono:wght@100&family=Domine&family=Nanum+Gothic&family=Noto+Serif+JP&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
  font-family: var(--main-font);
}

:root {
  --white: #ffffff;
  --black: #000000;
  --orange: #ff8400;
  --light-transparent: rgba(255, 255, 255, 0.25);
  --dark-transparent: rgba(0, 0, 0, 0.5);
  --header-font: "Nanum Gothic", sans-serif;
  --header-font-secondary: "Noto Serif JP", serif;
  --main-font: "Chivo Mono", monospace;
  --main-font-secondary: "Domine", serif;
  --transition-5s: 0.5 ease-in-out;
  --spacing-small: 1.2px;
  --spacing-large: 1.5px;
  --height-small: 1.2;
  --height-large: 1.5;
}

*::-webkit-scrollbar {
  display: none;
}

body {
  background: black;
  padding: 15px;
  letter-spacing: var(--spacing-small);
  line-height: var(--height-small);
}

.navbar {
  background: var(--white);
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  all: unset;
}

article {
  position: relative;
}

.transition {
  transition: 0.5s ease-in-out;
}

#work .img-container #click {
  /* display: initial; */
  position: absolute;
  top: 5%;
  margin: 0 auto;
  width: 6rem;
  height: 6rem;
  padding: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(5px);
  transition: 0.5s ease-in-out;
  animation: throb 1.5s infinite alternate;
  z-index: 600;
}

@keyframes throb {
  0% {
    transform: scale(1) translateY(0);
  }

  100% {
    transform: scale(1.1) translateY(-15px);
  }
}

/* navbar styling */

.navbar {
  width: 100%;
  position: relative;
  top: 0;
  z-index: 975;
  border-radius: 5px 5px 0 0;
  /* border-bottom: 1px solid var(--orange); */
}

.navbar a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  background-image: linear-gradient(
    to right,
    #db6400,
    var(--black),
    var(--orange)
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  font-weight: bold;
}

nav {
  width: 100%;
  min-height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 15px 45px;
}

nav .navigation-logo {
  width: 2.5rem;
}

nav ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

nav ul li {
  padding: 5px 15px;
  margin: 5px 10px;
  font-size: 18px;
  font-family: var(--main-font-alt);
  letter-spacing: 1.2px;
  cursor: pointer;
  border-bottom: 1px solid transparent;
  transition: 0.5s ease-in-out;
}

nav ul li:hover {
  border-bottom: 1px solid var(--orange);
  border-width: 1px;
}

nav ul .lets-talk-link {
  font-size: 22px;
  border-bottom: 1px solid transparent;
  background: var(--orange);
  color: var(--white);
  border-radius: 5px;
}

nav ul .lets-talk-link:hover,
#work .lets-talk-btn button:hover {
  transform: scale(1.05);
}

nav .toggler {
  cursor: pointer;
  display: none;
}

nav .toggler img {
  width: 3rem;
}

nav .nav-active {
  transform: translateX(0);
}

/* main component */

section {
  min-height: 100vh;
  width: 100%;
  background: var(--white);
}

#home,
#email {
  display: flex;
  align-items: center;
  justify-content: center;
}

#home .home-header {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}

#home .home-header h1 {
  font-size: 20rem;
  flex-basis: 50vh;
  background-image: url("./assets/orange-3.webp");
  background-position: 50%;
  background-size: cover;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}

#home .home-header h2 {
  font-size: 36px;
  flex-basis: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(
    to right,
    #db6400,
    var(--black),
    var(--orange)
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

#home .home-header h1, #home .home-header h2, #home .home-header .container {
  width: 100%;
}

#home .home-header .container {
  
  text-align: center;
  flex-basis: 20vh;
}

#home .home-header .container .text {
  font-size: 24px;
  margin: 120px 0 0;
  position: relative;
  color: var(--black);
  font-weight: bold;
}

/* Update the styles for .second-text element */
#home .home-header .text.second-text {
  position: relative; /* Add relative position to establish a new stacking context for ::before pseudo-element */
  overflow: hidden;
  color: var(--orange);
}

#home .home-header .text.second-text::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: var(--white);
  border-left: 2px solid var(--black);
  animation: animate 4s steps(26) infinite;
}

@keyframes animate {
  40%,
  60% {
    left: 100%;
  }

  100% {
    left: 0%;
  }
}

/* offerings component */

#offerings {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

#offerings .offerings-header {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#offerings header h1 {
  font-size: 66px;
  width: 100%;
  padding: 0 55px;
  text-align: center;
  background-image: linear-gradient(
    to right,
    #db6400,
    var(--black),
    var(--orange)
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

#offerings .offerings-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}

#offerings .offerings-container .left {
  flex-direction: row;
}

#offerings .offerings-container .right {
  flex-direction: row-reverse;
}

#offerings .offerings-container .offering-item {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  /* min-height: 100vh; */
  font-weight: bold;
}

#offerings .offering-item .desc-container,
#offerings .offering-item .img-container {
  width: 40%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
}

#offerings .offering-item .desc-container {
  text-align: left;
}

#offerings .offering-item .desc-container span {
  width: 100%;
  font-size: 48px;
  background-image: linear-gradient(
    to right,
    #db6400,
    var(--black),
    var(--orange)
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  padding-bottom: 10px;
}

#offerings .offering-item .desc-container p {
  width: 100%;
  font-size: 22px;
}

#offerings .offering-item .img-container img {
  width: 100%;
}

/* skills section */

#skills {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 20vh;
}

#skills .logos {
  overflow: hidden;
  padding: 60px 0;
  white-space: nowrap;
  position: relative;
}

#skills .logos:hover .logos-slide {
  animation-play-state: paused;
}

#skills .logos-slide {
  animation: 25s slide infinite linear;
  display: inline-block;
}

#skills .logos-slide img {
  /* width: 85px; */
  height: 85px;
  margin: 0 40px;
}

@keyframes slide {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}


/* work component */

#work {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 0 0 5px 5px;
}

#work .work-header {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#work header h1 {
  font-size: 66px;
  width: 100%;
  padding: 0 55px;
  text-align: center;
  background-image: linear-gradient(
    to right,
    #db6400,
    var(--black),
    var(--orange)
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

#work .works-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}

#work .work-item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 0 0 150px;
}

#work .works-container .right {
  flex-direction: row-reverse;
}

#work .works-container .left {
  flex-direction: row;
}

#work .works-container .left .desc-container {
  left: 75px;
}

#work .works-container .right .desc-container {
  right: 75px;
}

#work .works-container .left .img-container {
  margin-right: 75px;
}

#work .works-container .right .img-container {
  margin-left: 75px;
}

#work .desc-container {
  width: 30%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  position: relative;
  z-index: 555;
}

#work .desc-container span {
  font-size: 44px;
  font-weight: bold;
  background-image: linear-gradient(
    to right,
    #db6400,
    var(--black),
    var(--orange)
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  /* padding-bottom: 20px; */
  /* line-height: var(--height-large); */
}

#work .desc-container p {
  color: var(--black);
  font-size: 18px;
  font-weight: bold;
}

#work .desc-container .bg-container {
  border: 1px solid var(--orange);
  border-radius: 50%;
  padding: 50px 20px;
  width: 400px; /* or whatever width you want */
  height: 400px; /* or whatever height you want */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--light-transparent);
  backdrop-filter: blur(5px);
}

#work .work-item .img-container {
  position: relative;
  width: 65%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 5px;
}

#work .work-item .img-container:hover .visit-btn-container {
  visibility: visible;
  opacity: 1;
}

#work .work-item .img-container:hover #click {
  visibility: hidden;
}

#work .work-item .img-container img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

#work .work-item .img-container .visit-btn-container {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  background: var(--light-transparent);
  backdrop-filter: blur(5px);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.5s ease-in-out;
  border-radius: 5px;
}

@keyframes flip {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(360deg);
  }
}

#work .visit-btn-container:hover a {
  animation: flip 1s 1;
}

#work .visit-btn-container a {
  padding: 15px 20px;
  background: var(--white);
  color: var(--orange);
  border: 1px solid var(--orange);
  border-radius: 5px;
  font-weight: bold;
  font-size: 22px;
  transition: 0.5s ease-in-out;
  transform-style: preserve-3d;
}

#work .visit-btn-container a:hover {
  background: var(--orange);
  color: var(--white);
  border: 1px solid var(--white);
}

#work .lets-talk-btn {
  width: 100%;
  height: 300px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

#work .lets-talk-btn button {
  background: var(--orange);
  color: var(--white);
  font-size: 36px;
  padding: 10px 15px;
  border-radius: 5px;
  transition: 0.5s ease-in-out;
}

/* contact component */

.homepage-btn, .message-btn {
  z-index: 999;
}

.homepage-btn {
  position: fixed;
  top: 50%;
  left: 30px;
  width: 40px;
  height: 40px;
  background: var(--orange);
  padding: 5px;
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  border-radius: 50%;
  transition: 0.5s ease-in-out;
}

.homepage-btn:hover,
.message-btn:hover {
  transform: scale(1.1);
}

.message-btn {
  position: fixed;
  bottom: 2%;
  right: 30px;
  width: 40px;
  height: 40px;
  background: var(--orange);
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  border-radius: 50%;
  transition: 0.5s ease-in-out;
}

.message-btn img,
.homepage-btn img {
  width: 25px;
}

#contact {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
}

#contact .cover {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--dark-transparent);
  z-index: -100;
}

#contact .contact-header {
  display: flex;
  align-items: center;
  justify-content: center;
}

#contact .contact-form-header h2 {
  color: #ffffff;
  text-shadow: 1px 1px 1px var(--black);
  z-index: 100;
}

#contact .contact-layout-container {
  width: 100%;
  height: 90vh;
  max-height: 550px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
}

#contact .contact-layout-container .contact-container,
#contact .contact-layout-container .img-container {
  width: 40vw;
  max-width: 650px;
  height: 100%;
  overflow: hidden;
  z-index: 100;
}

#contact .contact-layout-container .img-container img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

#contact .contact-layout-container .contact-container {
  background-image: url("./assets/orange-3.webp");
  background-position: 50%;
  background-size: cover;
  /* backdrop-filter: blur(5px); */
  border-radius: 5px;
}

#contact .contact-form-header {
  width: 100%;
  text-align: center;
  padding: 20px 0;
  font-size: 22px;
  border-bottom: 1px solid var(--white);
}

#contact article {
  padding: 20px;
  font-weight: bold;
  font-size: 16px;
  color: var(--white);
}

#contact article,
#contact article ul,
#contact article ul li {
  width: 100%;
  height: 100%;
}

#contact article ul {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}

#contact article ul li img {
  width: 2.5rem;
  height: 2.5rem;
  padding: 5px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.55);
  backdrop-filter: blur(5px);
}

#contact article ul .moshe-info,
#contact article ul .contact-info,
#contact article ul .social-info {
  display: flex;
  align-items: center;
  justify-self: center;
  flex-direction: column;
  text-align: center;
}

#contact article ul .moshe-info span {
  width: 100%;
  margin: 5px 0;
}

#contact article ul .moshe-info span:first-child {
  font-size: 20px;
}

#contact article ul .contact-info a {
  margin: 5px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
}

#contact article ul .contact-info img {
  margin-right: 10px;
}

#contact article ul .social-info .social-links {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-top: 10px;
}

#contact article ul .social-info .social-links a {
  margin-right: 10px;
}

#contact article ul .contact-info a, #contact article ul .social-info .social-links a {
transition: 0.5s ease-in-out;
}

#contact article ul .contact-info a:hover, #contact article ul .social-info .social-links a:hover {
  transform: scale(1.1);
}

/* email component */

#email form {
  width: 100%;
  max-width: 1400px;
  height: 85vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding: 0 40px;
}

#email form .inputBox-container {
  width: 80%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}

#email form .inputBox-container .spacer {
  width: 90%;
  height: 1px;
  background: var(--orange);
}

#email form .inputBox {
  width: 100%;
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#email form .inputBox input {
  all: unset;
  width: 100%;
  height: 100%;
  padding: 0 0 0 7%;
  font-size: 34px;
  overflow: hidden;
  background: var(--white);
}

#email form .inputBox input::placeholder {
  font-size: 34px;
  color: var(--orange);
  background: var(--white);
}

#email form .inputBox .inputBox-message-input {
  font-size: 22px;
}

#email form .inputBoxBtn-Container {
  width: 20%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#email form .inputBoxBtn {
  font-size: 26px;
  padding: 10px 15px;
  border-radius: 15px;
  border: 1px solid var(--orange);
  color: var(--orange);
  transition: 0.5s ease-in-out;
}

#email form .inputBoxBtn:hover {
  transform: scale(1.1);
  color: var(--white);
  background: var(--orange);
  border: 1px solid var(--white);
}

@media screen and (max-width: 900px) {
  .navbar {
    position: relative;
    top: 0;
  }

  nav .toggler {
    display: block;
  }

  nav .toggler .menu-btn {
    transform: rotate(180deg);
    transition: 1s ease-in-out;
  }

  nav .toggler .turned-btn {
    transform: rotate(-270deg);
  }

  nav ul {
    position: fixed;
    top: 10vh;
    right: 0;
    width: 100%;
    height: 90vh;
    flex-direction: column;
    transform: translateX(100%);
    background-color: var(--white);
    opacity: 0.9;
    transition: 0.5s ease-in-out;
  }
}

@media screen and (max-width: 768px) {

* {
  font-family: var(--header-font-secondary);
}

  .navbar {
    position: fixed;
    top: 0;
    left: 0;
    background: var(--light-transparent);
    backdrop-filter: blur(5px);
  }

  nav {
    padding: 15px;
  }

  .message-btn {
    right: 25px;
    bottom: 25px;
  }

  body {
    padding: 0;
  }

  nav ul {
    background: var(--dark-transparent);
    backdrop-filter: blur(5px);
  }

  nav ul li {
    font-size: 22px;
    border-bottom: 1px solid transparent;
    background: var(--orange);
    color: var(--white);
    border-radius: 5px;
    cursor: pointer;
    border-bottom: none;
    transition: 0.5s ease-in-out;
  }

  /* home component */

  #home .home-header {
    justify-content: center;
    min-height: 0;
  }

  #home .home-header h1 {
    font-size: 12rem;
    width: 100%;
    text-align: center;
    white-space: nowrap;
    flex-basis: 30vh;
  }

  #home .home-header h2 {
    font-size: 16px;
    white-space: nowrap;
    width: 100%;
    text-align: center;
    flex-basis: 3vh;
    padding: 0 15px;
  }

  #home .home-header .container {
    flex-basis: 45vh;
    width: 100%;
    text-align: center;
    padding: 0 15px;
    line-height: var(--height-small);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  #home .home-header .container .text {
    margin: 0;
  }

  #home .home-header .container .first-text {
    display: block;
    font-weight: normal;
  }

#home .home-header .text.second-text {
  white-space: nowrap;
  font-size: 22px;
  font-weight: bold;
}

/* offerings component */

#offerings .offerings-header, #work .work-header {
  min-height: 10vh;
}

#offerings header h1, #work header h1 {
  font-size: 3.5rem;
  white-space: pre-wrap;
  width: 100%;
  text-align: center;
  padding: 0 15px;
}

#offerings .offerings-container, #work header .work-container {
  margin-top: 20vh;
}

#offerings .offerings-container .left, #offerings .offerings-container .right {
  flex-direction: column;
}

#offerings .offering-item .desc-container,
#offerings .offering-item .img-container {
  width: 100%;
  height: 60vh;
  padding: 0 15px;
}

#offerings .offering-item .desc-container {
  text-align: center;
}

#offerings .offering-item .desc-container span {
  font-size: 3rem;
  padding-bottom: 20px;
}

#offerings .offering-item .desc-container p {
  font-size: 18px;
  line-height: var(--height-large);
  letter-spacing: 1.2px;
}

/* skills component */

#skills {
  min-height: 0;
}

#skills .logos {
padding: 0;
}

/* work component */

#work .lazyload-wrapper, #work .img-container, #work .img-container img, #work .visit-btn-container {
  width: 100%;
}

#work .work-header {
  margin: 30vh;
}

#work .works-container .right, #work .works-container .left {
